/*global $, Modernizr */

var app = (function ($, Modernizr) {
    'use strict';

    return {
        config: {
            jsPath: '/assets/js'
        },

        init: function () {
            this.loadModules();
        },

        hasModule: function (moduleString) {
            return (typeof this.resolveModule(moduleString) === 'object');
        },

        loadModule: function (moduleString, callback) {
            var module,
                modulePath;

            module = this.resolveModule(moduleString);
            modulePath = moduleString.replace(/\./g, '/');

            if (module === null) {
                $.getScript(this.config.jsPath + '/' + modulePath + '.js').done(function () {
                    this.resolveModule(moduleString).init();
                }.bind(this));
            } else {
                module.init();
            }
        },

        loadModules: function () {
            var modules = $.trim($('html').attr('data-modules'));

            if (modules !== '') {
                modules.split('|').forEach(function (module) {
                    this.loadModule(module, null);
                }.bind(this));
            }
        },

        resolveModule: function (moduleString) {
            var module,
                modules,
                obj;

            modules = moduleString.split('.');
            module = modules.shift();
            obj = this;

            while (module) {
                if (obj.hasOwnProperty(module)) {
                    obj = obj[module];

                    if (!modules.length) {
                        return obj;
                    }
                }

                module = modules.shift();
            }

            return null;
        }
    };
}($, Modernizr));

$(function () {
    app.init();
});